<template>
	<view class="container">
		<view class="zero_nav" :style="{position:fixed?'fixed':'relative',height:navBarHeight+'px'}">
			<view class="tool_bar"
				:style="{'margin-top':statusBarHeight+menuBotton+'px',height:menuHeight+'px',width: capsule?toolBarWidth+'px':'100%'}">
				<view class="nav_left" v-if="capsuleLeft&&capsule" :style="{width:menuboxWidth+'px',lineHeight:menuHeight+'px'}">
					<slot name="left"></slot>
				</view>
				<view class="nav_center">
					<slot name="default"></slot>
				</view>
			<!-- 	<view class="nav_right" v-if="!capsule" :style="{width:rightWidth+'px'}">
					<slot name="right"></slot>
				</view> -->
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "zero-custom-bar",
		props: {
			fixed: {
				type: Boolean,
				default: true,
			},
			// 小程序用， TRUE:生成一个和右边胶囊对称的div
			capsuleLeft: {
				type: Boolean,
				default: true,
			},
			// 小程序用 true: 工具栏可用宽度为100%-右边胶囊宽度
			capsule: {
				type: Boolean,
				default: true,
			},
			leftWidth: {
				type: Number,
				default: 0,
			},
			// rightWidth: {
			// 	type: Number,
			// 	default: 101,
			// },
			// toolBarH: {
			// 	type: Number,
			// 	default: 32,
			// },
		},
		data() {
			return {
				statusBarHeight: 20,
				navBarHeight: 60,
				menuRight: 7,
				menuBotton: 4,
				menuHeight: 32,
				menuboxWidth: 101,
				toolBarWidth: 0,

			};
		},
		created() {
			this.getSysInfo()
			// 如果需要自定义左边盒子宽度就使用自定义值，否则对齐右边
			if(this.leftWidth){
				this.menuboxWidth = this.leftWidth
			}
		},
		methods: {
			getSysInfo() {
				const systemInfo = uni.getSystemInfoSync();
				// 获取胶囊按钮的位置
				const menuButtonInfo = uni.getMenuButtonBoundingClientRect();
				// 安全高度
				this.statusBarHeight = systemInfo.statusBarHeight
				// 导航栏高度 = 状态栏到胶囊的间距（ 胶囊距上距离 - 状态栏高度 ）*2  +  胶囊高度  +  状态栏高度
				this.navBarHeight = (menuButtonInfo.top - systemInfo.statusBarHeight) * 2 + menuButtonInfo.height +
					systemInfo.statusBarHeight;
				this.menuBotton = menuButtonInfo.top - systemInfo.statusBarHeight; // 胶囊距底部间距（保持底部间距一致）
				this.menuHeight = menuButtonInfo.height; // 胶囊高度（自定义内容可与胶囊高度保证一致）
				this.menuRight = systemInfo.screenWidth - menuButtonInfo.right; // 胶囊距右方间距
				this.menuboxWidth = menuButtonInfo.width + this.menuRight * 2; // 胶囊盒子宽度
				this.toolBarWidth = systemInfo.screenWidth - this.menuboxWidth; // 工具栏可用宽度 100% - 胶囊盒子宽度 
			},
		}
	}
</script>

<style lang="scss" scoped>
	$nav_size:32px;

	.zero_nav {
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		z-index: 999;
		background: #F8F8F8;
		overflow: hidden;
	}

	.tool_bar {
		background-color: #F8F8F8;
		display: flex;
		flex-flow: row nowrap;
	}

	.nav_left {
		height: 100%;
		flex-shrink: 0;
	}
	.nav_center{
		height: 100%;
		flex: 1;
	}
</style>
